.section {
  width: 400px;
  height: 100%;
  padding-top: 20px;
  position: relative;
}
.section-header {
  color: black;
  text-align: center;
  top: 22px;
  width: auto;
  font-weight: 500;
  left: 30px;
  position: absolute;
  background: white;
}
.content {
  margin: 10px;
  text-align: left;
  padding: 20px 10px 20px 10px;
  border: 2px solid #929292;
  border-radius: 5px;
}

@media all and (max-width: 400px){
  .section{
    width: 100vw;
  }
} 
span{
  font-family: 'Roboto';
}
.label{
  color: #555555;
  font-weight: 600;
}
.value{
  color: black;
  font-weight: 400;
}