label {
    text-transform: uppercase;
    font-weight: 700;
    font-size: 14px;
    color: #555555;
  }
  
  input {
    border-radius: 15px;
    border: 1px solid #b7b7b7;
    padding: 5px 5px 5px 10px;
    font-size: 15px;
    transition: 0.2s;
    
  }
  
  input:focus {
    outline: none;
    border: 1px solid #64d488;
  }
  
  input::placeholder {
    color: #bebebe;
    font-size: 14px;
  }