body {
  margin: 0;
  font-family: 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.container {
  width: 775px;
  margin: 0 auto;
  display: flex;
  padding: 2rem;
}
.info {
  display: flex;
  flex-direction: column;
}
@media all and (max-width: 775px) {
  .container {
    flex-direction: column;
    width: 90%;
  }
}

.cv {
  font-family: 'Roboto';
  font-weight: 900;
  font-size: 50px;
  margin: 10px;
  margin-bottom: 0;
  padding: 0;
}
