body {
  display: flex;
  justify-content: center;
}
h1{
  text-align: center;
}
.card {
  width: 300px;
  height: fit-content;
  padding: 15px;
  border-radius: 25px;
  display: flex;
  justify-content: center;
  background: #fff;
  animation: open 0.5s;
}

form {
  display: flex;
  flex-direction: column;
  gap: 5px;
}

.field {
  margin: 5px;
  display: flex;
  flex-direction: column;
}

input[type='file'] {
  display: none;
}

.custom-file-upload {
  border-radius: 50%;
  display: inline-block;
  position: relative;
  padding: 6px;
  cursor: pointer;
  background: #cacaca;
  margin: 0 auto;
  margin-bottom: 25px;
}

.img-wrap {
  position: relative;
  width: 200px;
  height: 200px;
  overflow: hidden;
  margin: 0 auto;
  border-radius: 50%;
}

.img-upload:before {
  content: '\f093';
  font-size: 90px;
  position: absolute;
  padding-top: 80px;
  margin: 0 auto;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  color: #cacaca;
  width: 200px;
  height: 200px;
  border-radius: 50%;
  opacity: 0;
  transition: 0.5s ease;
  background-color: #fff;
  -webkit-transform: translate(-50%, -50%);
  -moz-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  -o-transform: translate(-50%, -50%);
}

.img-upload:hover:before {
  opacity: 1;
}

img {
  width: 200px;
  height: auto;
  background-size: contain;
  background-position: top center;
}

.name {
  text-align: center;
  text-transform: uppercase;
  font-weight: 700;
  color: #474747;
  max-width: 220px;
  overflow-wrap: break-word;
}

.profession {
  text-align: center;
  max-width: 220px;
  overflow-wrap: break-word;
  color: #626262;
}

button {
  position: relative;
  color: #ffff;
  letter-spacing: 1px;
  font-size: 14px;
  margin: 20px auto;
  margin-bottom: 0;
  padding: 10px;
  text-align: center;
  transition: 0.5s;
  border-radius: 10px;
  cursor: pointer;
  border-radius: 25px;
  border: none;
  background: #188ebd;
  -webkit-transition: 0.5s;
  -moz-transition: 0.5s;
  -ms-transition: 0.5s;
  -o-transition: 0.5s;
}

.save {
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 1px;
  width: 90px;
  box-shadow: 4px 0px 14px 0px #63d3a6;
}

.edit {
  color: #fff;
  width: 110px;
}

button:hover {
  left: 0;
  color: #fff;
  width: 140px;
  box-shadow: 0px 0px 10px 0px #63d3a6;
}

button:focus {
  outline: none;
}
